	module.exports = {"pathContext":{"docHtml":"<h2>wide-align</h2>\n<p>A wide-character aware text alignment function for use in terminals / on the\nconsole.</p>\n<h3>Usage</h3>\n<pre><code>var align = require('wide-align')\n\n// Note that if you view this on a unicode console, all of the slashes are\n// aligned. This is because on a console, all narrow characters are\n// an en wide and all wide characters are an em. In browsers, this isn't\n// held to and wide characters like \"古\" can be less than two narrow\n// characters even with a fixed width font.\n\nconsole.log(align.center('abc', 10))     // '   abc    '\nconsole.log(align.center('古古古', 10))  // '  古古古  '\nconsole.log(align.left('abc', 10))       // 'abc       '\nconsole.log(align.left('古古古', 10))    // '古古古    '\nconsole.log(align.right('abc', 10))      // '       abc'\nconsole.log(align.right('古古古', 10))   // '    古古古'\n</code></pre>\n<h3>Functions</h3>\n<h4><code>align.center(str, length)</code> → <code>str</code></h4>\n<p>Returns <em>str</em> with spaces added to both sides such that that it is <em>length</em>\nchars long and centered in the spaces.</p>\n<h4><code>align.left(str, length)</code> → <code>str</code></h4>\n<p>Returns <em>str</em> with spaces to the right such that it is <em>length</em> chars long.</p>\n<h3><code>align.right(str, length)</code> → <code>str</code></h3>\n<p>Returns <em>str</em> with spaces to the left such that it is <em>length</em> chars long.</p>\n<h3>Origins</h3>\n<p>These functions were originally taken from\n<a href=\"https://npmjs.com/package/cliui\">cliui</a>. Changes include switching to the\nMUCH faster pad generation function from\n<a href=\"https://npmjs.com/package/lodash\">lodash</a>, making center alignment pad\nboth sides and adding left alignment.</p>","docPath":"argo-ci/node_modules/wide-align/readme","proj":"argo-ci"}}

